/* GENERAL */

@font-face {
  font-family: "MTOChaney";
  src: url(../public/assets/Font/MTOChaney.ttf);
}

* {
  margin: 0;
  padding: 0;
  background-color: #fcdc2a;
}

body {
  font-family: "MTOChaney", sans-serif;
}

html {
  scroll-behavior: smooth;
}

p {
  color: rgb(85, 85, 85);
}

/* TRANSITION */

a,
.btn {
  transition: all 300ms ease;
}

/* DESKTOP NAV */
nav,
.nav-links,
a,
.logo {
  background-color: black;
  display: flex;
}

nav {
  justify-content: space-around;
  align-items: center;
  height: 10vh;
}

.nav-links {
  gap: 2rem;
  list-style: none;
  font-size: 1.2rem;
}

a,
.logo {
  color: white;
  text-decoration: none;
}

a:hover {
  color: orange;
  text-decoration: underline;
  text-underline-offset: 1rem;
  text-decoration-color: rgb(181, 181, 181);
}

.logo {
  font-size: 2rem;
}

.logo:hover {
  cursor: default;
}

/* HAMBURGER MENU */

#hamburger-nav {
  display: none;
}

.hamburger-menu {
  position: relative;
  display: inline-block;
}

.hamburger-icon {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 24px;
  width: 30px;
  cursor: pointer;
  background-color: black;
}

.hamburger-icon span {
  width: 100%;
  height: 2px;
  background-color: white;
  transition: all 0.3 ease-in-out;
}

.menu-links {
  position: absolute;
  top: 100%;
  right: 0;
  background-color: white;
  width: fit-content;
  max-height: 0;
  overflow: hidden;
  transition: all 0.3 ease-in-out;
}

.menu-links a {
  display: block;
  padding: 10px;
  text-align: center;
  font-size: 1rem;
  color: white;
  text-decoration: none;
  transition: all 0.3 ease-in-out;
}

.menu-links li {
  list-style: none;
}

.menu-links.open {
  max-height: 300px;
}

.hamburger-icon.open span:first-child {
  transform: rotate(45deg) translate(10px, 5px);
}

.hamburger-icon.open span:nth-child(2) {
  opacity: 0;
}

.hamburger-icon.open span:last-child {
  transform: rotate(-45deg) translate(10px, -5px);
}

.hamburger-icon span:first-child {
  transform: none;
}

.hamburger-icon span:first-child {
  opacity: 1;
}

.hamburger-icon span:first-child {
  transform: none;
}

/* ICONS */

.icon {
  cursor: pointer;
  height: 2rem;
}

/* BUTTONS */

.btn-container {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.btn {
  font-weight: 600;
  transition: all 300ms ease;
  padding: 1rem;
  width: 8rem;
  border-radius: 2rem;
}

.btn-color-1,
.btn-color-2 {
  border: rgb(53, 53, 53) 0.1rem solid;
}

.btn-color-1:hover,
.btn-color-2:hover {
  cursor: pointer;
}

.btn-color-1,
.btn-color-2:hover {
  background: rgb(53, 53, 53);
  color: white;
}

.btn-color-1:hover {
  background: rgb(0, 0, 0);
}

.btn-color-2 {
  background: none;
}

.btn-color-2:hover {
  border: rgb(255, 255, 255) 0.1rem solid;
}

.btn-container {
  gap: 1rem;
  /* HERE */
  background-color: white;
}

/* SECTIONS */

section {
  padding-top: 4vh;
  height: 60vh;
  margin: 0 10rem;
  box-sizing: border-box;
  min-height: fit-content;
}

.section-container {
  display: flex;
}

/* PROFILE SECTION */

#profile {
  justify-content: center;
  gap: 5rem;
  height: 80vh;
}

#profile .section__pic-container {
  align-items: center;
  margin: 0 0rem;
  margin: auto 0;
}

.section__text {
  align-self: center;
  text-align: center;
}

.section__text p {
  font-weight: 600;
}

.section__text__p1 {
  text-align: center;
  font-size: 1.4rem;
}

.section__text__p2 {
  font-size: 1.75rem;
  margin-bottom: 1rem;
}

.title {
  font-size: 3rem;
  text-align: center;
}

#socials-container {
  display: flex;
  justify-content: center;
  margin-top: 1rem;
  gap: 1rem;
}

#profile .section__text .btn-container {
  gap: 1rem;
  /* HERE */
  background-color: #fcdc2a;
}

/* ABOUT SECTION */

#about {
  position: relative;
  height: 80vh;
}

.about-containers {
  gap: 2rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

/* HERE */
.about-containers .details-container h3,
.about-containers .details-container ul li {
  background-color: white;
}

.about-details-container {
  justify-content: center;
  flex-direction: column;
}

.about-containers,
.about-details-container {
  display: flex;
}

.about-pic {
  border-radius: 2rem;
}

.arrow {
  position: absolute;
  right: 0rem;
  /* bottom: 25rem; */
}

.details-container {
  padding: 2.5rem;
  background: white;
  border-radius: 2rem;
  border: rgb(53, 53, 53) 0.1rem solid;
  border-color: rgb(163, 163, 163);
  text-align: left;
}

.section-container {
  gap: 4rem;
  height: 80%;
}

#about .section__pic-container {
  margin: 0 0rem;
  display: flex;
  width: 400px;
  height: 400px;
  margin: auto 0;
  padding-bottom: 2rem;
}

/* EXPERIENCE SECTION */

#experience {
  position: relative;
  height: 80vh;
}

/* HERE */
.experience-sub-title,
.article-container p {
  background-color: white;
}

.experience-sub-title {
  color: rgb(85, 85, 85);
  font-weight: 600;
  font-size: 1.75rem;
  margin-bottom: 2rem;
}

.experience-details-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.article-container {
  margin-top: 10px;
  display: flex;
  text-align: initial;
  flex-wrap: wrap;
  flex-direction: row;
  gap: 1.5rem;
  justify-content: space-around;
  /* HERE */
  background: white;
}

article {
  /* display: flex; */
  width: 15rem;
  justify-content: space-around;
  /* HERE */
  background: white;
}

.article-container .h3 {
  background-color: black;
  margin-left: 20rem;
}

article .icon {
  /* HERE */
  background: white;
  cursor: default;
}

/* PROJECTS SECTION */

#projects {
  position: relative;
  height: 85vh;
}

.color-container {
  border-color: rgb(163, 163, 163);
  background: rgb(250, 250, 250);
}

.project-img {
  border-radius: 2rem;
  width: 100%;
  height: 100%;
}

.project-title {
  margin: 1rem;
  color: black;
}

.project-btn {
  color: black;
  border-color: rgb(163, 163, 163);
}

/* FUNFACT */
#funfact {
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 80vh;
  width: 80%;
  margin-top: 0vh;
}

.faq {
  margin-top: 2rem;
  padding-bottom: 1rem;
  border-bottom: 2px solid black;
  cursor: pointer;
  /* HERE */
  background: white;
}

.question {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* HERE */
  background: white;
}

.answer {
  max-height: 0;
  overflow: hidden;
  transition: max-height 1s ease;
  text-align: left;
  margin-left: 20px;
}

.faq.active .answer {
  max-height: 300px;
}

/* CONTACT */

#contact {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 30vh;
}

.contact-info-upper-container {
  justify-content: center;
  border-radius: 2rem;
  border: rgb(53, 53, 53) 0.1rem solid;
  border-color: rgb(163, 163, 163);
  background: (250, 250, 250);
  margin: 2rem auto;
  padding: 0.5rem;
}

.contact-info-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  margin: 1rem;
}

.email-link,
.linkedIn-link {
  color: black;
  background-color: #fcdc2a;
}

.contact-icon {
  cursor: default;
}

.email-icon {
  height: 2.5rem;
}

/* FOOTER SECTION */

footer {
  height: 10vh;
  margin: 0 0rem;
}

footer p {
  text-align: center;
}
