@media screen and (max-width: 1400px) {
  #profile {
    height: 50vh;
    margin-bottom: 6rem;
  }
  .about-containers {
    flex-wrap: wrap;
  }
  #contact,
  #projects {
    height: fit-content;
  }
}

@media screen and (max-width: 1200px) {
  #desktop-nav {
    display: none;
  }
  #hamburger-nav {
    display: flex;
  }
  #experience,
  .experience-details-container {
    margin-top: 1rem;
  }
  #profile,
  .section-container {
    display: block;
  }
  .arrow {
    display: none;
  }
  section,
  .section-container {
    height: fit-content;
  }
  section {
    margin: 0 5%;
  }
  .section__pic-container {
    width: 275px;
    height: 275px;
    margin: 0 auto 2rem;
  }
  .about-containers {
    margin-top: 0;
  }
}

@media screen and (max-width: 600px) {
  #contact,
  footer {
    height: 10vh;
  }
  #profile {
    height: 45vh;
    margin-bottom: 0;
  }
  .section__pic-container img {
    width: 100%;
    height: auto;
  }
  #about {
    position: relative;
    height: 120vh;
  }
  #about .section__pic-container {
    margin: 0 0rem;
    display: flex;
    width: 100%;
    height: auto;
    margin: auto 0;
  }
  #experience {
    height: 160vh;
  }
  #project {
    height: 70hv;
    margin-bottom: 0;
  }
  article {
    font-size: 1rem;
  }
  #funfact {
    position: relative;
  }
  #contact {
    height: 30vh;
  }
  footer nav {
    height: fit-content;
    margin-bottom: 1rem;
  }
  .about-containers,
  .contact-info-upper-container,
  .btn-container {
    flex-wrap: wrap;
  }
  .contact-info-container {
    margin: 0;
  }
  .contact-info-container p,
  .nav-links li a {
    font-size: 1rem;
  }
  .experience-sub-title {
    font-size: 1.25rem;
  }
  .logo {
    font-size: 1.5rem;
  }
  .nav-links {
    flex-direction: column;
    gap: 0.5rem;
    text-align: center;
  }
  .section__pic-container {
    width: auto;
    height: 46vw;
    justify-content: center;
  }
  .section__text__p2 {
    font-size: 1.25rem;
  }
  .title {
    font-size: 2rem;
  }
  .text-container {
    text-align: center;
    padding-bottom: 2rem;
  }
}
